<script>
    import { getData } from "../lib/api";

    let rate = 3;
    export let index;

    
    function renderStar(index, rate) {
        return index < rate ? 'yellow' : 'gray'
    }

    async function getRecipeData() {
        const recipeResponse = await getData(`/api/recipes/${index}`, null, null);
        return {
            rate : randomNumber(3) + 3,
            ...recipeResponse.data
        };
    }

    function randomNumber(num) {
        return Math.floor(Math.random() * num);
    }

    const recipe = getRecipeData();
    console.log(recipe);
</script>

<div id="post-wrap">
    {#await recipe}
        
    {:then recipe} 
    <div id="img-wrap">
        <img src={recipe.Steps[recipe.Steps.length-1].img} alt="fail"/>
    </div>
    <div id="info-wrap">
        <h1>{recipe.title}</h1>
        <!-- <h2>{recipe.}</h2> -->
        <div>
            <div id="ratings">
                {#each Array(5) as _, index}
                <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path fill={renderStar(index, recipe.rate)} d="M7.87839 13.2012L4.72038 14.8935L4.64697 14.9268C4.1106 15.1297 3.60476 15.0887 3.21941 14.7454C2.85776 14.4232 2.7268 13.9609 2.80173 13.4291L3.32965 9.78417L0.944645 7.27773C0.531642 6.85717 0.370914 6.34138 0.509865 5.80111C0.663812 5.20252 1.16938 4.85782 1.89814 4.74242L5.06817 4.26413L6.72803 0.825223C6.98585 0.340033 7.38841 0.0523974 7.89442 0.0659165C8.38972 0.0791496 8.7814 0.382367 9.08894 0.923136L10.6945 4.2877L14.2487 4.78153C14.8135 4.87901 15.2298 5.17315 15.3962 5.66366C15.5621 6.153 15.4042 6.64821 14.9785 7.15345L12.3983 9.78597L12.9774 13.4283C13.0695 14.058 12.9593 14.5572 12.538 14.8539C12.1588 15.121 11.6842 15.121 11.1383 14.9332L11.0481 14.8939L7.87839 13.2012Z"/>
                </svg>
                {/each}
                <p>({randomNumber(15) + 1})</p>
            </div>
            <div id="post-heart">
                <svg width="25" height="22" viewBox="0 0 25 22" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path fill="#FF6969" d="M17.0881 0.197197C19.7167 -0.41958 22.2933 0.4744 23.5063 1.98952C24.6259 3.38814 25.1348 4.84216 24.9695 6.98723C24.8504 8.53325 24.3443 9.67939 23.3461 10.9415C23.1621 11.1741 22.9609 11.4129 22.7073 11.7026C22.5582 11.8729 21.9937 12.5077 22.0223 12.4754C21.0233 13.6035 18.0782 16.4294 13.1432 20.999C12.7802 21.3351 12.2097 21.3335 11.8488 20.9952C7.61245 17.0244 4.87032 14.4098 3.61303 13.1419C1.29063 10.7999 0.408086 9.43896 0.0873364 7.24114C-0.248201 4.942 0.393314 3.05516 1.83552 1.5894C3.18797 0.214866 5.60662 -0.294486 7.99488 0.191072C9.3932 0.475366 10.8825 1.38993 12.4956 2.91622C13.8715 1.50529 15.4041 0.592349 17.0881 0.197197Z"/>
