<script>
    import Category from "./Category.svelte";
    import Element from "./Element.svelte";

    let selected = 0;
    const categoryList = {
        BEST: ["", "", "", ""],
        : ["", "", "/", "", "", "/", "//", "", "//", "//", "", "", "", "", "", "//", ""],
        : ["", "", "/", "", "", "", "", "", "", "", "", "", "", ""],
        : ["", "", "", "", "", "", "/", "", "", "", "", "", "", "/", "", ""],
        : ["", "", "", "", "", "", "", "", "", "", "", "", "", ""],
        : ["/", "/", "/", ""],
};
</script>

<div id="container">
    <div class="wrap" id="category-wrap">
        {#each Object.keys(categoryList) as category, index}
            <Category bind:selected {index}>{category}</Category>
        {/each}
    </div>
    <div class="wrap" id="element-wrap">
        {#if selected !== null}
            {#each Object.values(categoryList)[selected] as menu}
                <Element>{menu}</Element>
            {/each}
        {:else if selected === null}
            {#each Object.values(categoryList)[0] as menu}
                <Element>{menu}</Element>
            {/each}
        {/if}
    </div>
</div>

