<script>
    import Procedure from "../components/add/Procedure.svelte";
    import Filter from "../components/add/Filter.svelte";
    import Info from "../components/add/Info.svelte";
    import Ingredients from "../components/add/Ingredients.svelte";
    import Tag from "../components/add/Tag.svelte";
    import Footer from "../layouts/Footer.svelte";
    import { getData, postData } from "../lib/api.js";
    import { router } from "tinro"
    function handleFormSubmit(event) {
        const formData = new FormData(event.target);
        const formDataObj = Object.fromEntries(formData);
        postData("api/recipes", formDataObj, console.log);
    }
    async function showData() {
        const response = await fetch('/api/recipes');
        console.log(response);
    }
    showData();

    const img = "../../assets/images/image-placeholder.png";

    let info = {
        img,
        title: "",
        description: "",
    }

    let ingredients = [];
    let steps = [];

    async function sendFormData() {
        console.log(info, ingredients, steps);
        const recipesResponse = await postData("/api/recipes/", {title : info.title, description : info.description}, null);
        if(!recipesResponse.ok) {
            alert("  ")
            return;
        }
        const ingredientsResponse = await postData(`/api/recipes/${recipesResponse.createdRecipe.id}/ingredients`, ingredients, null);
        if(!ingredientsResponse.ok) {
            alert("  ")
            return;
        }
        let stepsSuccess = true;
        steps.forEach(async (step) => {
            const body = new FormData();
            for (let key in step) {
                if (step.hasOwnProperty(key)) {
                    body.append(key, step[key]);
                }
            }
            const stepResponse = await postData(`/api/recipes/${recipesResponse.createdRecipe.id}/steps`, body, null);
            if(!stepResponse.ok) stepsSuccess === false;
            console.log(stepResponse);
        })
        if(!stepsSuccess) {
            alert("  ")
            return;
        }

        router.goto("/")
        
        console.log({
            recipesResponse,
            ingredientsResponse,
        });
    }

</script>

<header>
    <h1> </h1>
</header>
<div id="body">
    <Info bind:info />
    <Filter />
    <Ingredients bind:ingredients />
    <Procedure bind:steps />
    <Tag />
</div>
<div on:click={sendFormData} id="footer">
    <p> </p>
</div>

