<script>
    import { onMount } from "svelte";

    let canvas;

    onMount(() => {
        const ctx = canvas.getContext('2d');
        canvas.width = 720;
        canvas.height = 280;
        let frame;

        const level = 10;
        const icebergX = 120;
        const icebergY = 20;
        const icebergSize = 50 + (level - 1) * 10;
        const image = new Image();
        image.src = 'build/assets/images/bear.png';

        const imageWidth = 50; //   
        const imageHeight = 50; //   
        let imageX = getRandomNumber(icebergX + 100, icebergX + icebergSize - imageWidth); //  x 
        let imageY = getRandomNumber(icebergY + 200, icebergY + icebergSize - imageHeight); //  y 

        const animationDuration = 5000; //    ()
        const animationDelay = 1000; //    ()
        let startTime = null; //   
        let currentTime = null; //  


        function animate(timestamp) {
            if (!startTime) {
                startTime = timestamp;
            }

            //  
            ctx.clearRect(0, 0, canvas.width, canvas.height);

            //  
            drawIceberg(icebergX, icebergY, icebergSize);
                
            //  
            ctx.drawImage(image, imageX, imageY, imageWidth, imageHeight);

            //   
            currentTime = timestamp - startTime;

            //   
            if (currentTime >= animationDelay) {
                imageX = getRandomNumber(icebergX, icebergX + icebergSize - imageWidth);
                imageY = getRandomNumber(icebergY, icebergY + icebergSize - imageHeight);
                startTime = timestamp;
            }

            //   
            requestAnimationFrame(animate);
        }
    
        function drawIceberg(x, y, size) {
            ctx.beginPath();
            ctx.moveTo(x, y);
            ctx.lineTo(x + size, y);
            ctx.lineTo(x + (size * 1.5), y + (size * Math.sqrt(3) / 2));
            ctx.lineTo(x + size, y + (size * Math.sqrt(3)));
            ctx.lineTo(x, y + (size * Math.sqrt(3)));
            ctx.lineTo(x - (size * 0.5), y + (size * Math.sqrt(3) / 2));
            ctx.closePath();

            //  
            ctx.fillStyle = '#a1cbf3';
            ctx.strokeStyle = '#b6d5f4';
            ctx.lineWidth = 1;

            ctx.fill();
            ctx.stroke();
        }

        //    
        function getRandomNumber(min, max) {
            return Math.random() * (max - min) + min;
        }

        requestAnimationFrame(animate)
    })
</script>

<canvas bind:this={canvas}/>
