<script>
    import { slide } from "svelte/transition";
    import InputWrap from './../../elements/InputWrap.svelte';
    import Section from "../../elements/Section.svelte";
    import InputImg from '../../elements/InputImg.svelte';
    import { postData } from "../../lib/api";

    export let steps;
    addProcedure();



    function addProcedure() {
        steps = [...steps, {
            img: "",
            description: "",
            ingredient: ""
        }]
    }

    async function handleSubmitSteps() {
        steps.forEach(async (step) => {
            const body = new FormData();
            for (let key in step) {
                // else if (step.hasOwnProperty(key)) body.append(key, step[key]);
                if (step.hasOwnProperty(key) && step[key] instanceof File) body.append(key, step[key], key);
                else if(step.hasOwnProperty(key)) body.append(key, step[key]);
            }
            console.log(body);
            const stepsResponse = await postData(`/api/recipes/10/steps`, body, null);
            console.log(stepsResponse);
        })
    }
</script>

<Section title="Procedure">
    {#each steps as step, index}
        <div in:slide class="inputWrap">
            <InputImg --width="48px" --height="48px" labelInfo={step + index} bind:selectedImage={step.img} />
            <InputWrap name="ingredient" placeholder="" bind:value={step.description} />
            <InputWrap name="amount" placeholder="" bind:value={step.ingredient} />
        </div>
    {/each}
    <div class="btnWrap">
        <button on:click={addProcedure}></button>
    </div>
</Section>

<style>
    div.inputWrap {
        display: flex;
        gap: 12px;
        align-items: center;
    }
    div.btnWrap {
        margin: 12px 0;
        display: flex;
        justify-content: center;
    }

    div.btnWrap > button {
        all:unset;
        background-color: #ff6969;
        color:white;
        border-radius: 10px;
        padding: 8px;
    }
</style>