<script>
    import { onMount } from 'svelte';
	import NavHorizon from '../components/nav/NavHorizon.svelte';
    import Footer from '../layouts/Footer.svelte';
    import { getData } from '../lib/api';
    import Ingredients from './../components/add/Ingredients.svelte';
    import { meta } from 'tinro';
    import Carousel from 'svelte-carousel';
    const route = meta();
    let index = route.params._index;

    async function getRecipeData() {
        const recipesResponse = await getData(`/api/recipes/${index}`, null, null);
        console.log(recipesResponse);
        if(!recipesResponse.ok) {
            alert(" ");
            goto("/");
            return;
        }
        console.log(`${recipesResponse.data.Steps[recipesResponse.data.Steps.length - 1].img.replace("uploads/", "")}`);
        return recipesResponse.data;
    }

    const data = getRecipeData();
    console.log(data);
    
</script>

