                <NavHorizon>
                    {#each data.Ingredients as ingredient}
                    <div class="label">
                        <div>{ingredient.name}</div>
                        <div>{ingredient.amount}</div>
                    </div>
                    {/each}
                </NavHorizon>
            </div>
            <div id="steps">
                <h1>Steps</h1>
                <div>
                    <Carousel>
                        {#each data.Steps as step, index}
