<script>
    import Topic from "../components/nav-topic/Topic.svelte";
    import NavHorizon from "../components/nav/NavHorizon.svelte";
    import TitleSub from "../elements/TitleSub.svelte";
    import Footer from "../layouts/Footer.svelte";
    import Header from "../layouts/Header.svelte";
    import { getData } from "../lib/api";

    const topics = ["", "", "", ""];

    async function getRecipeListData() {
        const recipesResponse = await getData("/api/recipes", null, null);
        const list = [];
        while(list.length < 5) {
            const randomIndex = Math.floor(Math.random() * recipesResponse.data.length) + 1;
            const randomRecipe = recipesResponse.data[randomIndex-1];
            if(list.some(obj => obj.id === randomRecipe.id)) continue;
            else {
                const randomRecipeData = await getData(`/api/recipes/${randomIndex}`, null, null);
                list.push({
                    img: randomRecipeData.data.Steps[randomRecipeData.data.Steps.length - 1].img.replace("uploads", ""),
                    title: randomRecipeData.data.title,
                    description: randomRecipeData.data.description,
                    id: randomRecipeData.data.id
                });
            }
        }
        return list;
    }
    const recipes = getRecipeListData();
</script>

<Header theme="light"/>
<div class="main">
    <img id="img-head" src="build/assets/images/header-1.png" alt="load failed"/>
    <div>
        <TitleSub> </TitleSub>
        <NavHorizon>
            {#each topics as topic}
            <Topic>{topic}</Topic>
            {/each}
        </NavHorizon>
    </div>
    <div>
        {#await recipes}
            <p></p>
        {:then recipes} 
        <TitleSub>BEST </TitleSub>
        <NavHorizon>
            {#each recipes as recipe}
            <div class="element">
                <a href={`/post/${recipe.id}`}>
                    <img src={recipe.img}/>
                    <p>{recipe.title}</p>
                </a>
            </div>
            {/each}
        </NavHorizon>
            
        {/await}
    </div>
    <!-- <Topics title="" topics={topics.reverse()} /> -->
    <!-- <Menu /> -->
</div>
<Footer />

