<script>
	import { slide } from 'svelte/transition';
    import Post from "./Post.svelte";
    import { getData } from '../lib/api';

    let items = [
        {name: " ", isVisible: false},
        {name: " ", isVisible: false},
        {name: " ", isVisible: false},
    ]

    function toggleValue(value) {
        value = !value;
    }
    async function getRecipeListData(count) {
        const recipesResponse = await getData("/api/recipes", null, null);
        const list = [];
        while(list.length < count) {
            const randomIndex = Math.floor(Math.random() * recipesResponse.data.length) + 1;
            const randomRecipe = recipesResponse.data[randomIndex-1];
            if(list.some(obj => obj.id === randomRecipe.id)) continue;
            else {
                const randomRecipeData = await getData(`/api/recipes/${randomIndex}`, null, null);
                list.push({
                    img: randomRecipeData.data.Steps[randomRecipeData.data.Steps.length - 1].img.replace("uploads", ""),
                    title: randomRecipeData.data.title,
                    description: randomRecipeData.data.description,
                    id: randomRecipeData.data.id
                });
            }
        }
        return list;
    }
    items = items.map((item) => {
        return {
            recipes: getRecipeListData(Math.floor(Math.random() * 4) + 1),
            ...item
        }
    })
    console.log(items);
</script>
<div id="posts-wrap">
    {#each items as item}
    <div on:click={() => {item.isVisible = !(item.isVisible)}}>
        <div>
            <h1>{item.name}</h1>
        </div>
        {#if item.isVisible}
            <div transition:slide>
                {#await item.recipes}
                    <p></p>
                {:then recipes}
                    {#each recipes as recipe}
                        <Post index={recipe.id}/>
                    {/each}
                {/await}
            </div>
        {/if}
    </div>
    {/each}
</div>

<style>
    h1 {
        font-size: 1.25rem;
        font-weight: 500;
        margin-bottom: 12px 0;
    }
    div#posts-wrap > div {
        cursor: pointer;
    }
</style>