<script>
    import InputWrap from './../../elements/InputWrap.svelte';
    import Section from "../../elements/Section.svelte";

    export let ingredients;
    addIngredients()



    function addIngredients() {
        ingredients = [...ingredients, {
            name: "",
            amount: ""
        }]
    }
</script>

<Section title="Ingredients">
    {#each ingredients as ingredient}
        <div class="input-wrap">
            <InputWrap name="ingredient" placeholder="" width="" bind:value={ingredient.name} />
            <InputWrap name="amount" placeholder="" width="" bind:value={ingredient.amount} />
        </div>
    {/each}
    <div class="btnWrap">
        <button on:click={addIngredients}></button>
    </div>
</Section>

