<script>
    export let selectedImage
    export let labelInfo;
    let imageValue = "../../build/assets/images/image-placeholder.png";
    function handleFileChange(event) {
        const file = event.target.files[0];
        const img = new FileReader();

        img.onloadend = () => {
            selectedImage = file;
            imageValue = img.result;
        }

        if(file) {
            img.readAsDataURL(file);
        }
    }
</script>

<div>
    <label for={labelInfo} id="profile-button">
        <div>
            <img src={imageValue} alt="error" />
        </div>
    </label>
    <input
        type="file"
        id={labelInfo}
        name={labelInfo}
        accept="image/*"
        on:change={handleFileChange}
    />
</div>

<style>
    label#profile-button {
        cursor: pointer;
    }
    label#profile-button > div > img {
        width: var(--width);
        height: var(--height);
        border-radius: 12px;
    }

    input[type="file"] {
        display: none;
    }
</style>

