<script>
    import Post from "../../elements/Post.svelte";
    import Video from "../../elements/Video.svelte";
    import { getData } from "../../lib/api";
    async function getRecipeListData() {
        const recipesResponse = await getData(`/api/recipes/`, null, null);
        if(!recipesResponse.ok) {
            alert(" ");
            goto("/");
            return;
        }
        console.log(recipesResponse.data);
        return recipesResponse.data;
    }

    const recipeList = getRecipeListData();
</script>
<div id="list">
    {#await recipeList}
        <p></p>
    {:then recipeList} 
    {#each recipeList as recipe}
    <div>
        <a href={`/post/${recipe.id}`}>
            <Post bind:index={recipe.id}/>
        </a>
    </div>
    {/each}
    {/await}
</div>

<style>
    div#list {
        padding: 12px;
    }
</style>