import Cookies from "js-cookie";

class ResponseError extends Error {
    ok;
    status;

    constructor(message, status) {
        super(message);
        this.status = status;
    }
}

export async function getData(url, body, callback) {
    try {
        const URL = body ? url + "?" + new URLSearchParams(body) : url;
        const response = await fetch(URL);
        const result = await response.json();
        if(!response.ok) throw new ResponseError(result.message, response.status);
        if(callback !== null) return callback(result);
        else return {
            ok:true,
            status: response.status,
            data: result
        };
    } catch(error) {
        return {
            ok: false,
            status: error.status,
            message: error.message
        }
    }
}

export async function postData(url, body, callback) {
    // const cookies = Cookies.get('connect.sid');
    // console.log(cookies);
    try {
        const response = await fetch(url, {
            method: "POST",
            // credentials: 'include',
            ...setContent(body)
        });
        const result = await response.json();
        if(!response.ok) throw new ResponseError(result.message, response.status);
        if(callback !== null) return callback(result);
        else return {
            ok:true,
            status: response.status,
            data: result
        };
    } catch(error) {
        return {
            ok: false,
            status: error.status,
            message: error.message
        };
    }
}

export async function updateData(url, body, callback) {
    try{
        const response = await fetch(url, {
            method: "UPDATE",
            ...setContent(body)
        });
        const result = await response.json();
        if(!response.ok) throw new ResponseError(result.message, response.status);
        if(callback !== null) return callback(result);
        else return {
            ok:true,
            status: response.status,
            data: result
        };
    } catch(error) {
        console.error(error);
        return null;
    }
}

export async function deleteData(url, body, callback) {
    try {
        const URL = body ? url + "?" + new URLSearchParams(body) : url;
        const response = await fetch(URL, {
            method: "DELETE",
            headers: {"Content-Type" : "application/json"},
            body: JSON.stringify(body)
        });
        const result = await response.json();
        if(!response.ok) throw new ResponseError(result.message, response.status);
        if(callback !== null) return callback(result);
        else return {
            ok:true,
            status: response.status,
            data: result
        };
    } catch(error) {
        console.error(error);
        return null;
    }
}

function setContent(body) {
    if(body instanceof FormData) return { body }
    else {
        return {
            body : JSON.stringify(body),
            headers: {
                "Content-Type" : "application/json"
            }
        }
    }
}